<template>
  <div class="login">
      <div class="wrap">
          <div class="content">
              <form  @submit.prevent="loginAction()" class="from-box">
                <Uinput 
                    label="아이디"
                    theme="w"
                    :help="id_help"
                    itype="input"
                    :value="id"
                    @chardEvent="(e)=>{id = e}"
                />

                <Uinput 
                    label="패스워드"
                    theme="w"
                    :help="pw_help"
                    itype="password"
                    :value="pw"
                    @chardEvent="(e)=>{pw = e}"
                />

                <br/>
                <button 
                    type="submit" 
                    class="U_btn U_full_btn" 
                >
                    로그인
                </button>
                <button 
                    type="button" 
                    class="U_btn U_full_btn" 
                    style="margin-top:10px" 
                    @click="
                        $store.state.controller.signup = true;
                    ">
                    회원가입
                </button>
                <button 
                    type="button" 
                    class="U_btn U_full_btn" 
                    style="margin-top:10px" 
                    @click="
                        $store.state.controller.login = false;
                        $store.state.controller.member = false;
                    ">
                    닫기
                </button>
              </form>
          </div>
      </div>
  </div>
</template>

<script>
import Uinput from "@/components/tag/Uinput";
export default {
    name:'login',
    components:{
        Uinput
    },
    data(){
        return{
            id : 'admin',
            pw : '1111',
            id_help : '',
            pw_help : ''
        }
    },
    methods:{
        async loginAction(){
            const data  = new Object()
            const send  = new Object()
            data.id     = this.id
            data.pw     = this.pw
            send.url    = 'login.json'  
            send.data   = data
            let token
            
            await this.$store.dispatch('AJAX_FROM',send).then((req)=>{
                if(req.data.state){
                    token = req.data.token
                    this.$store.state.member = req.data.info
                    this.$store.state.is_member = true
                    this.$store.state.controller.login = false
                    alert('로그인 완료되었습니다.')
                }else{
                    this.pw_help = '맞지않은 정보입니다.'
                }
            })
            await this.$cookies.set('token',token)
            await this.$store.dispatch('AJAX_SETTING_GET',this.$cookies.get('token'))
        }
    },
}
</script>

<style scoped>
    .login{
        width:100%;
        height:100%;
        left:0;
        top:0;
        position:fixed;
        background: rgba(0,0,0,0.7);
        color:#fff;
        z-index: 7000;
    }
    .wrap{
        display:table;
        width:100%;
        height:100%;
    }
    .content{
        display: table-cell;
        width:100%;
        height:100%;
        vertical-align: middle;
        text-align: center;
    }
    .from-box{
        max-width:640px;
        margin:0 auto;
        padding:15px;
        background: #fff;
        border:1px solid #d9d9d9
    }
</style>