<template>
  <div class="singup">
      <div class="content">
          <form  @submit.prevent="signupAction()" class="from-box">
            <Uinput 
                        label="아이디"
                        theme="w"
                        help=""
                        itype="input"
                        :value="form.id"
                        @chardEvent="(e)=>{form.id = e}"
            />

            <Uinput 
                        label="닉네임"
                        theme="w"
                        help=""
                        itype="input"
                        :value="form.nick"
                        @chardEvent="(e)=>{form.nick = e}"
            />

            <Uinput 
                        label="비밀번호"
                        theme="w"
                        help=""
                        itype="password"
                        :value="form.pw"
                        @chardEvent="(e)=>{form.id = e}"
            />

            <Uinput 
                        label="비밀번호 확인"
                        theme="w"
                        help=""
                        itype="password"
                        :value="form.pwr"
                        @chardEvent="(e)=>{form.pwr = e}"
            />

            <Uinput 
                        label="전화번호"
                        theme="w"
                        help=""
                        itype="input"
                        :value="form.phone"
                        @chardEvent="(e)=>{form.phone = e}"
            />
            
            <Uinput 
                        label="메일"
                        theme="w"
                        help=""
                        itype="input"
                        :value="form.mail"
                        @chardEvent="(e)=>{form.mail = e}"
            />

            <Uinput 
                        label="생년월일"
                        theme="w"
                        help=""
                        itype="date"
                        :value="form.birth"
                        @chardEvent="(e)=>{form.birth = e}"
            />

            <div class="clear btn_wrap">
                <button type="submit" class="U_btn">완료</button>
                <button type="button" class="U_btn" @click="$store.state.controller.signup = false">닫기</button>
            </div>

          </form>
      </div>
  </div>
</template>

<script>
import Uinput from '@/components/tag/Uinput';
export default {
    name:"Signup",
    components:{
        Uinput
    },
    data(){
        return{
            form:{
                id          : '',
                nick        : '',
                pw          : '',
                pwr         : '',
                phone       : '',
                mail        : '',
                birth       : ''
            }
        }
    },
    methods:{
        signupAction(){
            for(const key in this.form){
                if(this.form[key]){
                    alert('값을 입력해주세요!')
                    return false
                }
            }
        }
    }
}
</script>

<style lang="scss" scoped>
    .singup{
        @extend .U_ct_box02;
        width       :100%;
        height      :100%;
        position    :fixed;
        z-index     :9000;
        left        :0;
        top         :0;
        text-align  :center ;
        .content{
            width :100%;
            height:100%;
            background: rgba(0,0,0,0.7);
  
            .from-box{
                position: relative;
                background  :#fff;
                margin      :0 auto;
                width       :100%;
                padding     :30px;
                padding-bottom :70px;
                max-width   :640px;
                max-height  :90%;
                overflow    :auto;
            }      
            .btn_wrap{
                position :absolute;
                width    :calc(100% - 60px);
                bottom   :30px;
                left     :30px;
                button:first-child {
                    float       :left;
                    border-color:#333;
                    color       :#fff;
                    background  : #333;
                }
                button:last-child {
                    float:right;
                }
                button{
                    border-radius:3px;
                    height       :30px;
                    width        :45%
                }
            }

        }
    }
</style>