<template>
  <div class="loading">
    <div class="wrap">
        <div class="content">
            <span class="icon">
                <font-awesome-icon far icon="snowboarding" />
            </span>
            <span class="txt">
                {{msg ? msg : '로딩중입니다'}}
                {{text}}
            </span>
        </div>
    </div>      
  </div>
</template>

<script>
export default {
    name:'loading',
    data(){
        return{
            text:''
        }
    },
    props:{
        state:Boolean,
        msg:String
    },
    mounted(){
        setInterval(()=>{
            this.text.length < 6 ? this.text += '.' : this.text = '' 
        },700)
    }
}
</script>

<style scoped lang="scss">
    .loading{
        z-index:99999;
        width:100%;
        height:100%;
        left:0;
        top:0;
        position:fixed;
        background: rgba(0,0,0,0.9);
        color:#fff
    }
    .wrap{
        display:table;
        width:100%;
        height:100%;
    }
    .content{
        display: table-cell;
        width:100%;
        height:100%;
        vertical-align: middle;
        text-align: center;
    }
    .icon{
        font-size:40px;
        line-height: 70px;
        display:inline-block;
        margin-bottom:15px;
        border:3px solid #fff;
        border-radius: 50%;
        width:70px;
        height:70px;
        text-align: center;
        > * {
            vertical-align: initial;
        }
    }
    .txt{
        font-size:0.9rem;
        display: block;
        
    }
</style>