<template>
  <div class="video">
      <VideoDetail 
        @closeEvent="(e)=>{view = e}"
        :url="url"
        v-if="view"
      />
      <ul class="list">
        <li v-for="(item,id) in list" :key="id">
          <button 
            type="button"
            @click="
              view = true;
              url  = item.b_media;
            "
          >
          <img :src="`https://img.youtube.com/vi/${item.b_media}/sddefault.jpg`" alt="">
          <span>{{item.b_subejct}}</span>
          <span>{{item.b_wdate}}</span>
          </button>
        </li>
      </ul>
  </div>
</template>

<script>
import VideoDetail from "@/components/videoDetail"
export default {
  name:"Video",
  components:{
    VideoDetail
  },
  data(){
    return {
      view : false,
      url  : "maY4TAmdAZU",
      list : [] 
    }
  },
  async mounted(){
    await this.$store.dispatch('AJAX_VIDEOLIST_GET').then(data=>{
      this.list = data.list
    })
  },
}
</script>

<style lang="scss" scoped>
  .video{width:100%}
  .list{
    @extend .clear;
    li {
      margin-top: 10px;
      float     : left;
      width     : 50%;

      img {
        width :100%;
        height: auto;
      }
    }
  }

</style>
