<template>
    <div class="member">
        <div class="clear">
            <button type="button" class="profile">
                
            </button>
            <div class="T_fl_lt info" >
                <div class="clear">
                    <span class="T_fl_lt">{{$store.state.member.user_nick}}</span>
                    <span class="T_fl_lt"> [{{grade}}]</span>
                    <span class="T_fl_rt">
                        <button type="button">
                            <font-awesome-icon far icon="user-cog" />
                        </button>
                    </span>
                </div>
                <div class="T_ft_rem T_ft_rem8">
                    <span>{{$store.state.member.user_name}} / </span>
                    <span>{{$store.state.member.user_id}} / </span>
                    <span>{{$store.state.member.user_birth}}</span>
                </div>
            </div>
        </div>
        <div>
            <ul class="tab_btn">
                <li :class="tab==0 ? 'on' :''" :style="{ width : admin ? '33.33%' : '50%'}"><button @click="tab=0" type="button">
                    내가쓴글
                </button></li>
                <li :class="tab==1 ? 'on' :''" :style="{ width : admin ? '33.33%' : '50%'}"><button  @click="tab=1" type="button">
                    강습
                </button></li>
                <li :class="tab==2 ? 'on' :''" :style="{ width : admin ? '33.33%' : '50%'}" v-if="$store.state.member.user_dvs == 3"><button @click="tab=2" type="button" >
                    샵관리
                </button></li>
                <li :class="tab==3 ? 'on' :''" :style="{ width : admin ? '33.33%' : '50%'}" v-if="$store.state.member.user_dvs == 4"><button @click="tab=3" type="button">
                    문의관리
                </button></li>
            </ul>
            <ul class="tab_con">
                <li v-if="tab == 0">
                    <strong>내가쓴글</strong>
                    <ul>
                        <li></li>
                    </ul>
                </li>
                <li v-if="tab == 1">
                    <strong>강습</strong>
                    <ul>
                        <li></li>
                    </ul>
                </li>
                <li v-if="$store.state.member.user_dvs == 3 && tab == 2">
                    <strong>샵</strong>
                    <ul>
                        <li></li>
                    </ul>
                </li>
                
                <li v-if="$store.state.member.user_dvs == 4 && tab == 3">
                    <strong>문의</strong>
                    <ul>
                        <li></li>
                    </ul>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
export default {
    name:'Member',
    computed:{
        grade(){
            return this.$store.state.config.user_dvs[this.$store.state.member.user_dvs]
        },
        admin(){
            return this.$store.state.member.user_dvs == 4 || this.$store.state.member.user_dvs == 3 ? true : false
        }
    },
    data(){
        return {
            tab : 0
        }
    }
}
</script>

<style lang="scss" scoped>
    .member{
        padding:30px;

        .profile{
            @extend .T_fl_lt;
            width         : 80px;
            height        : 80px;
            border-radius : 50%;
            background    : #d9d9d9 ;
        }
        .info{
            width        : calc(100% - 80px);
            padding      : 10px;
        }

        .tab_btn{
            @extend .clear;
            margin-top    : 20px;
            margin-bottom : 20px;
            height        : 50px;
            position      : relative;
            .on button{
                background:#333;
                color     :#fff;
                border    :none
            }
            > * {
                float     :left;
                height    :100%;
                button{
                    @extend .T_ft_rem9;
                    width       :100%;
                    height      :100%;
                    background  :#fff;
                    border      :1px solid #d9d9d9
                }
            }
        }
        .tab_con{
            > strong{
                @extend .T_ft_rem12;
            }
        }
    }
</style>