<template>
    <div class="tag-box" :class="theme">
        <label :for="name ? name : RamdumId" >{{label}}</label>
        <input 
            :type="itype" 
            v-if="
                itype=='input' || 
                itype=='date' || 
                itype=='password'
                " 
            v-model="setValue"
            :name="name ? name : RamdumId"
            :id="name ? name : RamdumId" 
            @change="sendData" 
        >

        <select
            v-if="itype=='select'" 
            v-model="setValue"
            :name="name ? name : RamdumId" 
            :id="name ? name : RamdumId" 
            @change="sendData"
        > 
            <option 
                :value="val" 
                :key="index"
                v-for="(val,index) in opVlaue"
            >
                {{opName[index]}}
            </option>
        </select>
        <p v-if="help" class="help">
            {{help}}
        </p>
        <br/>
    </div>
</template>

<script>
export default {
    name:'Uinput',
    data(){
        return{
            setValue: ''
        }
    },
    props:{
        help    :String,
        value   :String,
        name    :String,
        opVlaue :Array,
        opName  :Array,
        label   :String,
        itype   :String,
        theme   :String
    },
    methods:{
        sendData(){
            this.$emit('chardEvent',this.setValue)
        }
    },
    mounted(){
        this.setValue = this.value
    },
    computed: {
        /*
        setValue: function () {
            return this.value
        },
        */
        RamdumId : function(){
            return `ran${String(Math.random()).replace(".","")}`
        }
    }
}
</script>

<style scoped>
    .tag-box{
        display:block;
        padding:5px 0;
    }
    .tag-box.w{
        color:#000;
        background: #fff;
    }
    .tag-box.d{
        color:#fff;
        background: #000;
    }
    input,
    select{
        border:1px solid #d9d9d9;
        padding:3px 5px;
        width:100%;
        font-size:0.85rem;
        padding:5px 10px;
        border:1px solid #d9d9d9;
        border-radius: 3px ;
        color:#333;
        font-size:0.75rem;
    }
    label{
        display:block;
        margin-bottom:7px;
        text-align: left;
        font-size:0.8rem;
    }
    .help{
        padding:7px;
        font-size:0.75em;
        border-left:5px solid #333;
        margin-top:3px;
    }
    .w .help{
        background: #f9f9f9;
    }
    .d .help{
        background: #333
    }

    .hide_label label{
        width:0;
        height:0;
        overflow:hidden;
        padding:0;
        margin:0;
    }

</style>>
