<template>
    <nav class="menu">
        <ul>
            <li v-for="(data,id) in menu" :key="id">
                <button type="button" @click="chage(data.name)">
                    <span class="icon">
                        <font-awesome-icon far :icon="data.icon" />
                    </span>
                    <span class="txt">{{data.text}}</span>
                </button>
            </li>            
        </ul>
    </nav>
</template>

<script>
export default {
    data(){
        return{
            menu:[
                {
                    name:'map',
                    icon:'map-marked-alt',
                    text:'스팟&샵 정보'
                },
                {
                    name:'video',
                    icon:'video',
                    text:'영상모음'
                },
                {
                    name:'community',
                    icon:'comments',
                    text:'커뮤니티'
                },
                {
                    name:'member',
                    icon:'user',
                    text:'마이페이지'
                }
            ]
        }
    },
    methods:{
        chage(key){
            this.$store.state.controller.video      = false
            this.$store.state.controller.member     = false
            this.$store.state.controller.community  = false
            this.$store.state.controller.map        = false
            switch (key) {
                case 'video':
                    this.$store.state.controller.video      = true
                break
                case 'member':
                    this.$store.state.controller.member     = true
                break
                case 'community':
                    this.$store.state.controller.community  = true
                break
                default:
                    this.$store.state.controller.map        = true
                break
            }
        },
    }

}
</script>

<style scoped lang="scss">
    .menu{
        background: #fff;
        position: absolute;
        bottom:0;
        max-width: 640px;
        width:100%;
        margin: 0 auto;
        left:0;;
        right:0;
        height:60px;
        box-shadow: 0 -2px 12px #8c8c8c;
        ul{
            @extend .clear;
            height:100%;
        }

        li{
            text-align: center;
            float:left;
            width:25%;
            height:100%;
            button {
                width: 100%;
                height: 100%;
            }
            button > * {display: block;}
        }
    }

    .icon{
        font-size:17px;
    }
    .txt{
        font-size:12px;
        font-weight: 500;
    }
</style>