<template>
  <div class="app-board">

    <!--loding          Page-->
    <Loading v-if="$store.state.controller.loading" />

    <!--login           Page-->
    <Login 
      v-if="
        (
          $store.state.controller.member || 
          $store.state.controller.login
        ) && !$store.state.is_member
      "
    />

    <!--Singup          Page-->
    <Signup v-if="$store.state.controller.signup"/>

    <!--Menu            Page-->
    <Menu />

    <!--Menu:Video      Page-->
    <Video v-if="$store.state.controller.video" />

    <!--Menu:Community  Page-->
    <Community  v-if="$store.state.controller.community" />

    <!--Menu:Map        Page-->
    <Map  v-if="$store.state.controller.map"  />

    <!--Menu:Member     Page-->
    <Member  v-if="$store.state.controller.member && $store.state.is_member" />

  </div>
</template>

<script>
import Login      from '@/components/login'
import Loading    from '@/components/loading'
import Menu       from '@/components/menu'
import Video      from '@/components/video'
import Community  from '@/components/community'
import Map        from '@/components/map'
import Member     from '@/components/member'
import Signup     from '@/components/signup'
export default {
  name: 'App',
  components: {
    Login,
    Loading,
    Menu,
    Video,
    Community,
    Map,
    Member,
    Signup
  },
  computed:{

  },
  async created(){
    await this.$store.dispatch('AJAX_SETTING_GET',this.$cookies.get('token'))
  },
  methods:{
    async logout(){
        const data = new Object()
        const api_send = new Object()
        data.token = this.$cookies.get('token')
        api_send.data = data
        api_send.url = 'userAjaxLogout.php'
        alert('로그아웃 완료')
        await this.$store.dispatch('AJAX_FROM',api_send)
        await this.$cookies.get('token')
        await this.$store.dispatch('AJAX_SETTING_GET',this.$cookies.get('token'))
        this.$store.state.controller.login = false
    },
    numberR(i){
      return i < 10 ? '0'+i : i
    }
  }
}
</script>

<style lang="scss">

body,html{
  height:100%;
  width :100%;
  color :#777;
  background:#000;
}

.app-board{
  overflow-x:hidden;
  position: relative;
  min-height: 100%;
  z-index   :9999;
  max-width: 640px;
  background: #fff;
  margin:0 auto;
  padding-bottom:60px
}

</style>
